.stack-cards {
  --stack-cards-gap: var(--space-sm);
  margin-right: 5%;
  margin-left: 5%; }
  .stack-cards :nth-child(odd) div {
    background-color: lightblue; }
  .stack-cards :last-child {
    margin-bottom: 5em; }

.stack-cards__item {
  position: relative;
  position: sticky;
  top: var(--space-md);
  height: 0;
  padding-bottom: 50%;
  transform-origin: center top; }
  .stack-cards__item > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

