.media-wrapper {
  position: relative;
  height: 0;
  padding-bottom: 56.25%; // 16:9 aspect ratio

  > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

@each $aspect-ratio in $media-wrapper-aspect-ratios {
  $aspect-ratio-x: nth($aspect-ratio, 1);
  $aspect-ratio-y: nth($aspect-ratio, 2);

